import React from "react";
import { Link, graphql, useStaticQuery } from "gatsby";
import Img from "gatsby-image";

import { Container, Button, Grid } from "@material-ui/core";

import "./NotFound.scss";


export default () => {
  const data = useStaticQuery(graphql`
    query {
      file(relativePath: { eq: "taken.jpg" }) {
        childImageSharp {
          fluid(maxWidth: 334) {
            ...GatsbyImageSharpFluid
          }
        }
      }
    }
  `);

  return (
    <Container className="not-found-container">
      <Grid
        container
        justify="center"
        alignItems="center"
        spacing={8}
      >
        <Grid item className="message-container" xs={12} sm={6} md={"auto"}>
          <h3 className="spaced-evenly">Oops! The page you were looking for does not exist.</h3>
          <p className="spaced-evenly">You may have misstyped the address or the page may have moved.</p>
          <Button
            className="spaced-evenly"
            component={Link}
            href="/"
            variant="contained"
            color="primary"
            size="small"
          >
            Go to homepage
          </Button>
        </Grid>
        <Grid item xs={12} sm={6} md={4}>
          <Img fluid={data.file.childImageSharp.fluid} fadeIn={false} className="not-found-img" />
        </Grid>
      </Grid>
    </Container>
  )
}
